import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            isReject: false,
            appeal: '药吃完了',
            haveMed: true,
            info: null
        }
    },
    created(){
        this.pull()
    },
    methods:{
        pull(){
            this.$post('Prescription/Prescription/GetPrescriptionApply',{
                Id: 1
            }).then(o => {
                this.info = o
                this.$store.dispatch('pushpatient', o.PatientInfo)
            })
        },
        toPre(){
            const ListDetail = this.info.PrescriptionFullInfo ? this.info.PrescriptionFullInfo.ListDetail : null
            this.$store.dispatch('pushupList',ListDetail)
            this.$router.push('/prescription')
        }
    }
}



